import React from 'react'
import * as yup from 'yup'
import { useFormik } from 'formik'
import TextInput from '@/components/TextInput'
import PasswordInput from '@/components/PasswordInput'
import { LoadingButton } from '@mui/lab'
import Paths from '@/layout/Paths'
import { Link as RouterLink, useNavigate } from "react-router-dom"
import { Box, Button, Dialog, DialogActions, DialogContent, DialogContentText, DialogTitle, FormControlLabel, Link, Radio, RadioGroup, Alert } from '@mui/material'
import { AccountType } from '@/enums'
import useRegister from '@/query/account/useRegister'

type Props = {}

const Register = (props: Props) => {
    const action = useRegister()
    const navigate = useNavigate()
    const [type, setType] = React.useState<AccountType>(AccountType.Individual)
    const validationSchema = yup.object({
        username: yup.string().trim().required("").matches(/^[0-9a-zA-Z]+$/, "").min(3, "3"),
        password: yup.string().trim().required("").matches(/^[0-9a-zA-Z]+$/, "").min(6, "6"),
        // https://stackoverflow.com/a/9204568
        email: yup.string().trim().required("").matches(/^[^\s@]+@[^\s@]+\.[^\s@]+$/, "EMAIL")
    })
    const handleClick = () => {
        navigate(Paths.ACCOUNT_LOGIN)
    }
    const handleTypeChange = (event: React.ChangeEvent<HTMLInputElement>) => {
        let value = (event.target as HTMLInputElement).value as AccountType
        setType(value)
    }

    const formik = useFormik({
        initialValues: {
            username: "",
            password: "",
            email: ""
        },
        validationSchema: validationSchema,
        onSubmit: (values) => {
            let username = values.username.trim().toLowerCase()
            let password = values.password.trim()
            let email = values.email.trim().toLowerCase()
            action.mutate({
                username,
                password,
                email,
                account_type: type
            })
        }
    })
    return (
        <Box marginX="auto" bgcolor="#F7F7F7" width="580px" marginY="60px" paddingY="60px" paddingX="80px">
            <form onSubmit={formik.handleSubmit} autoComplete="off">
                <p style={{ color: "#333333", fontSize: "30px", lineHeight: "45px" }}></p>
                <TextInput name='username' label="" formik={formik} sx={{ bgcolor: "white" }}></TextInput>
                <TextInput name='email' label="Email" formik={formik} sx={{ bgcolor: "white" }}></TextInput>
                <PasswordInput name='password' label="" formik={formik} sx={{ bgcolor: "white" }}></PasswordInput>
                <Box paddingY={2}>
                    <RadioGroup
                        value={type}
                        onChange={handleTypeChange}
                        row={true}
                    >
                        <FormControlLabel value={AccountType.Business} control={<Radio />} label="" />
                        <FormControlLabel value={AccountType.Individual} control={<Radio />} label="" />
                    </RadioGroup>
                </Box>
                {action.isError && <Alert severity="error" sx={{ marginBottom: 1 }}>{action.error.message}</Alert>}
                <LoadingButton
                    loading={action.isLoading}
                    size="large"
                    type="submit"
                    variant="contained"
                    fullWidth
                >
                    
                </LoadingButton>
                <p style={{ paddingTop: "20px", fontSize: "14px" }}> <Link underline="hover" component={RouterLink} to={Paths.ACCOUNT_LOGIN}></Link></p>
            </form>
            <Dialog open={action.isSuccess} sx={{
                '& .MuiDialogContent-root': {
                    padding: "8px",
                    width: "500px"
                },
                '& .MuiDialogActions-root': {
                    padding: "8px",
                },
            }}>
                <DialogTitle></DialogTitle>
                <DialogContent>
                    <DialogContentText sx={{ paddingLeft: 2 }}>
                        
                    </DialogContentText>
                </DialogContent>
                <DialogActions>
                    <Button onClick={handleClick}></Button>
                </DialogActions>
            </Dialog>
        </Box>
    )
}

export default Register