/**  */
export const searchProducts = async (option: IProductsFilter) => {
    // 
    let param: any = {}
    param.page = option.page
    param.size = option.size
    if (option.is_second_hand != null) {
        param.is_second_hand = option.is_second_hand.toString()
    }
    if (option.industry) {
        param.industry = option.industry
    }
    if (option.price) {
        switch (option.price) {
            case PriceNumType.a:
                // 
                break;
            case PriceNumType.b:
                param.price__lt = 25
                break;
            case PriceNumType.c:
                param.price__gte = 25
                param.price__lt = 50
                break;
            case PriceNumType.d:
                param.price__gte = 50
                param.price__lt = 100
                break;
            case PriceNumType.e:
                param.price__gte = 100
                param.price__lt = 200
                break;
            case PriceNumType.f:
                param.price__gte = 200
                break;
            default:
                break;
        }
    }
    const response = await http.get('spus', param)
    const data = response.data as IJsonResult<IPagingResult<IProductView>>
    return data.content
}

/**  */
export const addProductComment = async ({ targetId, body }: IAddComment): Promise<IProductComment> => {
    const response = await http.post('comment/spus', {
        spu: targetId,
        body
    })
    const data = response.data as IJsonResult<IProductComment>
    if (data.success) {
        return data.content
    } else {
        throw new Error(data.message)
    }
}


