import React from 'react'
import ProgressWithLabel from './ProgressWithLabel'

type Props = {}

/**  */
const RatingLevels = (props: Props) => {
    return (
        <React.Fragment>
            <ProgressWithLabel label='' max={100} value={68}></ProgressWithLabel>
            <ProgressWithLabel label='' max={100} value={17}></ProgressWithLabel>
            <ProgressWithLabel label='' max={100} value={5}></ProgressWithLabel>
            <ProgressWithLabel label='' max={100} value={3}></ProgressWithLabel>
            <ProgressWithLabel label='' max={100} value={7}></ProgressWithLabel>
        </React.Fragment>
    )
}

export default RatingLevels